<template>
  <div class="register">
    <div class="Form_Box">
      <div class="logo">
        <Logo :size="100"/>
      </div>
      <div class="Border_Top_Box">
        <h4 class="reg_text">Register</h4>
        <el-form label-position="top" ref="registerForm" :rules="rules" label-width="80px" :model="formLabelAlign" class="form">
          <el-form-item label="Email" prop="email">
            <el-input v-model="formLabelAlign.email"></el-input>
          </el-form-item>
          <el-form-item label="Email Code" prop="email_code">
            <el-col :md="20" :xs="15" :sm="18">
              <el-input v-model="formLabelAlign.email_code" placeholder="please click button at right to send code to your email"></el-input>
            </el-col>
            <el-col :md="4" :xs="9" :span="6">
              <el-button type="primary" icon="el-icon-message">Send Code</el-button>
            </el-col>
          </el-form-item>
          <el-form-item label="Password" prop="password">
            <el-input v-model="formLabelAlign.password"></el-input>
          </el-form-item>
          <el-form-item label="Password Confirmtation" prop="password2">
            <el-input v-model="formLabelAlign.password2"></el-input>
          </el-form-item>
          <p>
            <el-checkbox v-model="checked">I agree with the terms and conditions</el-checkbox>
          </p>
          <el-button type="primary" class="Form_Submit" @click="handleSubmit('registerForm')" size="nomal">Register</el-button>
        </el-form>
      </div>
      <Footer></Footer>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      formLabelAlign: {},
      checked: false,
      rules: {
        email: [
          { required: true, message: '请输入Email', trigger: 'blur' },
        ],
        email_code: [
          { required: true, message: '请输入Email Code', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入Password', trigger: 'blur' },
        ],
        password2: [
          { required: true, message: '请确认Password', trigger: 'blur' },
        ]
      }
    }
  },
  methods: {
    handleSubmit (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import url('../../assets/css/var.less');
.register {
  background: @reg_bg;
  min-height: 100vh;
  padding-bottom: 40px;
  .logo {
    margin: 50px auto;
  }
  .reg_text {
    color: @color;
  }
  .form {
    margin-top: 40px;
  }
}
</style>